<template>
	<div class="home">
        <h1>小程序项目管理</h1>
        <ul>
            <li v-for="(item, index) in items" :key="index" @click="jump(item.code)">
                <img :src="item.img" alt="">
                <span>{{item.name}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    data() {
        return {
            items: [
                {
                    code: 0,
                    img: 'https://mp.weixin.qq.com/wxopen/basicprofile?action=get_headimg&token=667832946&t=20180917173154',
                    name: 'gogogo'
                },
                {
                    code: 1,
                    img: 'https://mp.weixin.qq.com/wxopen/basicprofile?action=get_headimg&token=828260424&t=20180917172923',
                    name: '爆笑段子Lite'
                }
            ]
        }
    },
    methods: {
        jump(code) {
            if(code == 0) {
                this.$router.push('/mp/view/management')
            } else if (code ==1) {
                this.$router.push('/mp/view/baoxiao')
            }
        }
    }
}
</script>


<style scoped>
.home h1 {
    margin-bottom: 30px;
}

ul {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

ul li {
    text-align: center;
    padding: 0 100px;
    cursor: pointer;
}
img {
    width: 200px;
}
span {
    font-size: 36px;
    color: rgb(0, 122, 234);
    display: block;
    margin-top: 30px;
}
</style>
