<template>
    <div class="baoxiao">
        <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="code">
                <el-input v-model="form.code"></el-input>
            </el-form-item>
            <el-form-item label="路径">
                <el-input v-model="form.path"></el-input>
            </el-form-item>
            <el-form-item label="主题">
                <el-input v-model="form.values.keyword1.value"></el-input>
            </el-form-item>
            <el-form-item label="内容类型">
                <el-input v-model="form.values.keyword2.value"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="success" @click="send">发送消息</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
export default {
    data() {
        return {
            form: {
                code: '',
                path: '',
                values: {
                    keyword1: {
                        value: ''
                    },
                    keyword2: {
                        value: ''
                    },
                    keyword3: {
                        value: ''
                    }
                }
            }
        };
    },
    mounted() {
        var d = new Date();  
        var datetime=d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds(); 
        this.form.values.keyword3.value = datetime
    },
    methods: {
        send() {
            let data = this.form
            this.$api.get("https://nyzda.top/template/send", data);
        }
    }
};
</script>

<style scoped>
.baoxiao {
    width: 500px;
    margin: auto;
}
</style>
