import Vue from 'vue'
import Router from 'vue-router'
import Management from './views/gogogo/management.vue'
import Item from './views/gogogo/Item.vue'
import Baoxiao from './views/baoxiao/Baoxiao.vue'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/mp/view/',
      name: 'home',
      component: Home
    },
    {
      path: '/mp/view/baoxiao',
      name: 'baoxiao',
      component: Baoxiao
    },
    {
      path: '/mp/view/management',
      name: 'management',
      component: Management
    },
    {
      path: '/mp/view/add',
      name: 'add',
      component: Item
    },
    {
      path: '/mp/view/update',
      name: 'update',
      component: Item
    }
  ]
})
